import React, { useState, useEffect, useRef } from 'react';
import { Candidate } from '../types';

interface MentionsInputProps {
  value: string;
  onChange: (value: string) => void;
  placeholder?: string;
  className?: string;
  candidates: Candidate[];
}

const MentionsInput: React.FC<MentionsInputProps> = ({
  value,
  onChange,
  placeholder = 'Type @ to mention someone...',
  className = 'form-input',
  candidates
}) => {
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [suggestions, setSuggestions] = useState<Candidate[]>([]);
  const [cursorPosition, setCursorPosition] = useState(0);
  const [mentionStart, setMentionStart] = useState(-1);
  const inputRef = useRef<HTMLTextAreaElement>(null);

  const handleInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newValue = e.target.value;
    const newCursorPosition = e.target.selectionStart || 0;

    onChange(newValue);
    setCursorPosition(newCursorPosition);

    // Check for @ mentions
    const textBeforeCursor = newValue.substring(0, newCursorPosition);
    const atIndex = textBeforeCursor.lastIndexOf('@');

    if (atIndex !== -1 && atIndex === textBeforeCursor.length - 1) {
      // @ is at the end of text before cursor
      setMentionStart(atIndex);
      setShowSuggestions(true);
      setSuggestions(candidates);
    } else if (atIndex !== -1) {
      // There's text after @
      const query = textBeforeCursor.substring(atIndex + 1).toLowerCase();
      if (query.length > 0) {
        const filtered = candidates.filter(candidate =>
          candidate.name.toLowerCase().includes(query)
        );
        setSuggestions(filtered);
        setShowSuggestions(true);
      } else {
        setSuggestions(candidates);
        setShowSuggestions(true);
      }
      setMentionStart(atIndex);
    } else {
      setShowSuggestions(false);
      setMentionStart(-1);
    }
  };

  const handleSuggestionClick = (candidate: Candidate) => {
    if (mentionStart === -1) return;

    const beforeMention = value.substring(0, mentionStart);
    const afterMention = value.substring(cursorPosition);
    const mention = `@${candidate.name}`;
    const newValue = beforeMention + mention + ' ' + afterMention;

    onChange(newValue);
    setShowSuggestions(false);
    setMentionStart(-1);

    // Focus back to input and set cursor position
    setTimeout(() => {
      if (inputRef.current) {
        const newCursorPos = beforeMention.length + mention.length + 1;
        inputRef.current.focus();
        inputRef.current.setSelectionRange(newCursorPos, newCursorPos);
      }
    }, 0);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (!showSuggestions || suggestions.length === 0) return;

    if (e.key === 'Escape') {
      setShowSuggestions(false);
      setMentionStart(-1);
    } else if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
      e.preventDefault();
      // Could implement keyboard navigation here
    } else if (e.key === 'Enter' && showSuggestions) {
      e.preventDefault();
      if (suggestions.length > 0) {
        handleSuggestionClick(suggestions[0]);
      }
    }
  };

  const handleBlur = () => {
    // Delay hiding suggestions to allow clicks
    setTimeout(() => setShowSuggestions(false), 150);
  };

  return (
    <div className="mentions-input-container relative">
      <textarea
        ref={inputRef}
        value={value}
        onChange={handleInputChange}
        onKeyDown={handleKeyDown}
        onBlur={handleBlur}
        placeholder={placeholder}
        className={className}
        rows={3}
      />

      {showSuggestions && suggestions.length > 0 && (
        <div className="mentions-suggestions absolute z-10 bg-white border border-gray-300 rounded-md shadow-lg max-h-48 overflow-y-auto w-full mt-1">
          {suggestions.slice(0, 5).map((candidate) => (
            <div
              key={candidate.id}
              className="px-3 py-2 hover:bg-gray-100 cursor-pointer border-b border-gray-100 last:border-b-0"
              onClick={() => handleSuggestionClick(candidate)}
            >
              <div className="font-medium text-sm">{candidate.name}</div>
              <div className="text-xs text-gray-500">{candidate.email}</div>
            </div>
          ))}
          {suggestions.length > 5 && (
            <div className="px-3 py-2 text-xs text-gray-500 border-t">
              And {suggestions.length - 5} more...
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default MentionsInput;
